// Fix Constant double up bug
    // * Whenever youd click the constant button again after the first time on the same signal, it would restart the sound and throw the algorithm out of whack
    //TODO: if you come along more bugs, just put them into the default export, make each statement a function and just run the functions inside the master function
export default function DblUpBugFix(){
    function dsblPntr(){
        document.querySelector('.audio-control-btn.cos.pointer-none').classList.remove("pointer-none"); 
        document.querySelector('.audio-control-btn.cos.on').classList.add("pointer-none");
    }
    document.querySelectorAll('.audio-control-btn.cos').forEach( item => item.addEventListener("click", (e) => {
        if(!e.target.classList.contains("pointer-none")){
            setTimeout(100, dsblPntr() );
        }    
    }));
}