import React from 'react'
import { configRangeScript } from '../Audio_Functions/Audio_Range_Config'
import {stopAudio,tglSgPlayBtn} from '../Audio_Functions/Audio_Global_Functions.js'
import { 
    sinewaveNodeArray, sgConfig, sineWaveLowsNode, 
    sineWaveMidsNode, sineWaveHighsNode, sineWaveHigh2LowNode, sineWaveLow2HighNode,
    initSgConfing, pinkNoiseHigh2Low,pinkNoiseLow2High,
    whiteNoiseHigh2Low,whiteNoiseLow2High, whiteNoiseNode, pinkNoiseAxSrc
} from '../Audio_Functions/Audio_Sg_Init'
import { makeETarget } from '../../../Tools/codeTools'
//import { AudioContext, createBiquadFilter, createMediaElementSource } from 'standardized-audio-context';
//******** REACT MARKUP *********** 
class PlayStopBtn extends React.Component {
    constructor(props){
        super(props);
        this.state = { 
            type: "play-stop"
        }
    }
    
    componentDidMount(){
        initSgConfing(this.state.type);   
        triggerPlayStop();  
    }
    render() {
        return (
            <button className="play-stop-btn off"><span>PLAY</span></button>
        )
    }
}

//************************
//TRIGGER - PLAY/STOP FUNCTION 
var noRpt = 1
var notifySafariUser = false;
function triggerPlayStop(){
    debugger;
    console.log("trigger started");

    //play currently selected sound
    var cnfgCntr = 1;
    document.querySelector(".play-stop-btn").addEventListener("click", () => {
        if(cnfgCntr == 1){ configRangeScript(); cnfgCntr++ }
        playStopSound();   
        if(window.safari != undefined && !notifySafariUser){ // else if browser is safari, alert user white and pink noise EQ don't work
            alert("Unfortunately the frequency selection tool will not work for white and pink noise in Safari, please use any other modern browser for these features. Thanks :-)")
            notifySafariUser = true;
         }
    });

    //when "play" is triggered on a noise node (white/pink), fire off audio API functionality (eq etc)
    document.querySelectorAll(".audio-source.constant.noise").forEach(node => {

        //add "play" listener for both pink and white noise
        node.addEventListener("play", (e) => { //e refers to target of the node being played (eg white noise)
            debugger;
            var mstrAudioLmnt = document.querySelector(".mstr-audio-node");
            if (mstrAudioLmnt == null || mstrAudioLmnt.classList.contains("sg-on") && noRpt < 3){ //if initial spoof is being played or sg mode is on, run functionality
                // dont run in safari due to bug
               if( window.safari == undefined) {AxAPIHandle(e);}  //if browser not safari, run APIHandle
            //    else if(window.safari != undefined){ // else if browser is safari, alert user white and pink noise EQ don't work
            //        alert("Unfortunately the frequency selection tool will not work for white and pink noise in Safari, please use any other modern browser for these features. Thanks :-)")
            //     }
                noRpt = 3;
            }
        });
    }); 

    //audio spoofer, this will trigger the filters for noise (white/pink) to start working (otherwise you need to click play first before the filters start working)
    
    var audioSpoofInit = function (){
        console.log("eeeeed")
        if (noRpt == 1){
            setTimeout(() => { 
                [document.querySelector(".mstr-audio-node")].forEach(source => {
                    source.volume = 0; source.play();
                    stopAudio([source]);
                    source.volume = 1;
                })
                document.removeEventListener("click",audioSpoofInit());     
                noRpt = 2;
            }, 100);
        }
    }
    
    //run the function once the user interacts with the page 
    document.querySelector(".main-nav-link.Audio").addEventListener("click", () => audioSpoofInit());
}

//************************ 
//PLAY/STOP FUNCTION
    //"CLICK", PLAY BUTTON
        function playStopSound(){ //plays or stops currently selected sound
debugger;
            var playStopBtn = document.querySelector(".play-stop-btn");

            // "CLICK" OFF -> ON - if play-stop-btn btn is .off, turn on relevant sound by checking sgConfig
            if(playStopBtn.classList.contains("off")){

                switch (sgConfig.AXSource[0]){ 
                    case "sine wave": // if sinewave is selected source
                        if (sgConfig.character == "constant"){ //constant is selected character
                            //connect sineWave nodes to their first filter & update sgConfig
                            sinewaveNodeArray.forEach( item => item.play());
                        }else if(sgConfig.character == "sweeping") {
                            //because sweeping sinewave is a single audio file, not multiple like constant
                            sgConfig.AXSource[1].load(); sgConfig.AXSource[1].play();
                        } 
                    break;
                    case "pink noise": //if pinknoise is selected source
                        sgConfig.AXSource[1].load(); sgConfig.AXSource[1].play();
                    break;
                    case "white noise": //if white noise is selected source
                        sgConfig.AXSource[1].load(); sgConfig.AXSource[1].play();
                    break;
                    default:
                }
                    tglSgPlayBtn("play"); //change play btn classes and text appropriately
            }

            // "CLICK" ON-> OFF - if play-stop-btn btn is .oN, turn off relevant sound by checking sgConfig
            else if(playStopBtn.classList.contains("on")){
                switch (sgConfig.AXSource[0]){       
                    case "sine wave": // if sinewave is selected source, turn off sound(s)
                        if (sgConfig.character == "constant"){ //constant is selected character
                            //connect sineWave nodes to their first filter & update sgConfig
                            //sinewaveNodeArray.forEach( item => stopAudio(item) ); 
                            stopAudio(sinewaveNodeArray);
                        } else if (sgConfig.character == "sweeping" && document.querySelector(".cos.high-to-low").classList.contains("on")){ //sweeping is selected character
                            stopAudio([sgConfig.AXSource[1]]); 
                        } else if (sgConfig.character == "sweeping" && document.querySelector(".cos.low-to-high").classList.contains("on")){ //sweeping is selected character
                            stopAudio([sgConfig.AXSource[1]]);
                        }
                    break;
                    case "pink noise": //if pinknoise is selected source, turn off sound(s)
                        stopAudio([sgConfig.AXSource[1]]);
                    break;
                    case "white noise": //if white noise is selected source, turn off sound(s)
                        stopAudio([sgConfig.AXSource[1]]);
                    break;
                    default:
                }
                    tglSgPlayBtn("stop"); //change play btn classes and text appropriately
            }
        }
        export {PlayStopBtn}

        //handles sounds that use the audio API (white/pink noise) - feeding audio src into AX API nodes (eq) to be adjusted when user desires
        var noRpt0 = 0;
        var noRpt1 = 0;
        var lowFreq; var midFreq; var highFreq
        var pageLoaded = setInterval(() => {
            if (document.querySelector(".audio-control-btn.low-frequency") != null){
                lowFreq = document.querySelector(".audio-control-btn.low-frequency");
                midFreq = document.querySelector(".audio-control-btn.mid-frequency");
                highFreq = document.querySelector(".audio-control-btn.high-frequency");
                clearInterval(pageLoaded);
            }
        }, 200);

        function AxAPIHandle(e){
            debugger;
            console.log("this thing kind aworks" + sgConfig);
            //NOTE: eTarget refers to the wrap of the "clicked element", e.target refers to whatver triggered this function (either pink or white noise)
            let eTarget = makeETarget(e);

            let audioCtx = new (AudioContext ||
                window.webkitAudioContext ||
                window.mozAudioContext ||
                window.oAudioContext ||
                window.msAudioContext); //new AudioContext(); 
            

            var lowPsFltr; var midNtchFltr; var highPsFltr;
            lowPsFltr = audioCtx.createBiquadFilter(); lowPsFltr.type = "lowshelf"; lowPsFltr.frequency.value = 400;
            midNtchFltr = audioCtx.createBiquadFilter(); midNtchFltr.type = "peaking"; midNtchFltr.frequency.value = 1000; midNtchFltr.Q.value = .5;
            highPsFltr = audioCtx.createBiquadFilter(); highPsFltr.type = "highshelf"; highPsFltr.frequency.value = 5000;

            // scan over frequeinces and check what filter btns have been clicked and to reduce necesarry frequencies (used when switching between sources)
            if (lowFreq != null){ //so.. eg if queryLowFrequency is off, make -12, if on, make 0 
            //low freq check
                (lowFreq.classList.contains("off")) ? lowPsFltr.gain.value = -12 : lowPsFltr.gain.value = 0;
            //mid freq check 
                (midFreq.classList.contains("off")) ? midNtchFltr.gain.value = -12 : midNtchFltr.gain.value = 0;   
            //high freq check
                (highFreq.classList.contains("off")) ? highPsFltr.gain.value = -12 : highPsFltr.gain.value = 0;  
            }

            //create pan node for master audio
            var masterAudioPanNode = audioCtx.createStereoPanner();

            //feed currently selected audio source into a audio source node
            let AxSrcNode = audioCtx.createMediaElementSource(sgConfig.AXSource[1]);
            //filters - create and adjust accordingly

            //connect necesarry filters for inital start up - "CONNECT" SOURCE -> FILTERS -> SPEAKERS
            AxSrcNode.connect(highPsFltr).connect(midNtchFltr).connect(lowPsFltr).connect(masterAudioPanNode).connect(audioCtx.destination); // create EQ chain

            //add PAN functionality
                //event listener for the pan strings (LCR), when clicked changed the pan value accordingly
                document.querySelectorAll(".lr-ctrl.node").forEach(node => node.addEventListener("click",(e) => { 
                    //if left button clicked
                        if(e.target.classList.contains("left")){
                            masterAudioPanNode.pan.value = -1;
                        }
                    //if center button clicked
                        if(e.target.classList.contains("center")){
                            masterAudioPanNode.pan.value = 0;
                        }
                    //if right button clicked
                        if(e.target.classList.contains("right")){
                            masterAudioPanNode.pan.value = 1;
                        }
                }) );

            //loop through frequencies and "click" funct for each. This will be where frequencies are attenuated/deattenuated when relevant btns are clicked and source is playing
            document.querySelectorAll(".frequency.wrap").forEach(frqBtn => {
                frqBtn.addEventListener("click", (e) => {
                    //console.log("in here")
                    let eTarget = makeETarget(e);        
                    //console.log(`freq is e.target.dataset.frequency`)
                    switch (eTarget.dataset.frequency){    
                        case "high": 
                            if(eTarget.classList.contains("off")){
                                highPsFltr.gain.value = -12;
                            }else{
                                highPsFltr.gain.value = 0;
                            }
                            break;
                        case "mid":
                            if(eTarget.classList.contains("off")){
                                midNtchFltr.gain.value = -12;
                            }else{
                                midNtchFltr.gain.value = 0;
                            }
                            break;
                        case "low":
                            if(eTarget.classList.contains("off")){
                                lowPsFltr.gain.value = -12;
                            }else{
                                lowPsFltr.gain.value = 0;
                            }
                            break;
                        default: break;       
                    }         
                })
            });
        }