import React, { Suspense } from 'react'
import { GenrlToggleOnOff } from '../Audio_Functions/Audio_Global_Functions'
import { leftRightAudioFunction } from './LR_Functions'

//Audio_Range Component
class LeftRightCtrl extends React.Component {
    constructor(props){
        super(props);
        this.state = { }
        this.stereoBtnsFunctionality = this.stereoBtnsFunctionality.bind(this);
    }

    stereoBtnsFunctionality(e){
        console.log("yes im trying LR")
        GenrlToggleOnOff(e.target,".lr-ctrl"); //toggle on/off to produce dimmed / lightened up btns 
    }

    componentWillMount(){

    }

    componentDidMount(){
        document.querySelectorAll(".lr-ctrl.node").forEach(item => item.addEventListener("click",(e) => this.stereoBtnsFunctionality(e)) ); // color functionality
        leftRightAudioFunction(); // panning functionality
    }

    render() {
        
      return (
        //Audio tools wrap
        <div id="left-right-ctrl-wrap" className="lr-ctrl wrap" aria-label="left right or center audio">
          <Suspense fallback={<div style={{display: "none"}}>loading</div>}>
            <button className="lr-ctrl node left off">L</button><button className="lr-ctrl node center on">C</button><button className="lr-ctrl node right off">R</button>
          </Suspense>
        </div>
      );
    }
}

export {LeftRightCtrl}