
//create and connect nodes once
//add an event listener to the buttons in that intial function at the end which has access to the panner node
//from here each LCR button will trigger master  and the 3 sine waves in its chosen direction

function leftRightAudioFunction(){ //sinewave only (mster is in play_stop)
    var audioCtx = new AudioContext();
//sinewave audio API Config
    //high
    var sineHighAxNode = audioCtx.createMediaElementSource(document.querySelector(".audio-source.sine-wave-high"));
    var sineHighPanNode = audioCtx.createStereoPanner();
    sineHighAxNode.connect(sineHighPanNode).connect(audioCtx.destination) 
    //mid
    var sineMidAxNode = audioCtx.createMediaElementSource(document.querySelector(".audio-source.sine-wave-mid"));
    var sineMidPanNode = audioCtx.createStereoPanner();
    sineMidAxNode.connect(sineMidPanNode).connect(audioCtx.destination) 
    //low
    var sineLowAxNode = audioCtx.createMediaElementSource(document.querySelector(".audio-source.sine-wave-low"));
    var sineLowPanNode = audioCtx.createStereoPanner();
    sineLowAxNode.connect(sineLowPanNode).connect(audioCtx.destination) 

    //event listener for the pan strings (LCR), when clicked changed the pan value accordingly
    document.querySelectorAll(".lr-ctrl.node").forEach(node => node.addEventListener("click",(e) => { 
        //if left button clicked
            if(e.target.classList.contains("left")){
                [/*masterAudioPanNode,*/sineHighPanNode,sineMidPanNode,sineLowPanNode].forEach( panNode => {
                    panNode.pan.value = -1;
                });
            }
        //if center button clicked
            if(e.target.classList.contains("center")){
                [/*masterAudioPanNode,*/sineHighPanNode,sineMidPanNode,sineLowPanNode].forEach( panNode => {
                    panNode.pan.value = 0;
                });
            }
        //if right button clicked
            if(e.target.classList.contains("right")){
                [/*masterAudioPanNode,*/sineHighPanNode,sineMidPanNode,sineLowPanNode].forEach( panNode => {
                    panNode.pan.value = 1;
                });
            }
    }) );
}

export { leftRightAudioFunction }